/* blury background  */

.bg-image {
  /* Add the blur effect */
  filter: blur(1px);
  -webkit-filter: blur(1px);

  /* Full height */
  /* height: 80%; */

  /* Center and scale the image nicely */
  /* background-position: center;
  background-repeat: no-repeat !important;
  background-size: cover; */
  margin-top: 2rem;
  margin-bottom: 2rem;
  height: 100%;
  padding: 10%;

  background-image: url('./sunrise3.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

/* Position text in the middle of the page/image */
.bg-text {
  /* background-color: rgb(0, 0, 0); Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 45%;
  left: 55%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 45%;
  padding: 19px;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  opacity: 0.9;
}
/* sample image galery */
.commission-samples {
  margin-top: 3rem;
  display: flex;
  width: 80%;
  overflow-x: scroll;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  justify-content: center;
}
.commission-samples img {
  margin-right: auto;
  margin-left: auto;
  max-width: 100%;
  max-height: 300px;
  padding: 8px;
  margin-left: auto;
  margin-right: auto;
}
/* testimonial */
.testimonials {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
.testimonials h1 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-weight: 400;
}
.testimonials p {
  color: rgb(97, 94, 94);
  font-size: 16px;
}
@media screen and (max-width: 768px) {
  .bg-image {
    width: 100%;
  }
  .bg-text {
    top: 26%;
    left: 50%;
    width: 43%;
    padding: 16px;
  }
  .hero-image-container h1 {
    font-size: 1.5rem;
  }
  .hero-image-container p {
    font-size: 1.1rem;
  }
}

hr {
  width: 90%;
  color: rgb(91, 89, 89);
  margin-right: auto;
  margin-left: auto;
  margin-top: 1rem;
}

@media screen and (max-width: 680px) {
  .bg-text {
    top: 25%;
    left: 50%;
    width: 50%;
    padding: 10px;
  }
  .hero-image-container h1 {
    font-size: 1rem;
  }
  .hero-image-container p {
    font-size: 0.7rem;
  }
}
